<template>
  <div v-bind:class="`${showPostLink ? 'card col-4 col-sm-3 m-0 p-0' : 'card col-12 m-0 p-0'} ${editMode ? '' : 'border-0' }`">

    <!-- CARD OPTIONS -->       
    <div v-if="editMode" class="card-header py-0 my-0 bg-secondary">
      <div class="row">
        <span @click="onDelete"><h6><i class="fa fa-trash" style="color: white; margin: 10px; cursor: pointer;"/></h6></span>
        <span v-if="hasMorePreviews" @click="changePreview"><h6><i class="fa fa-refresh" style="color: white; margin: 10px; cursor: pointer;"/></h6></span>
        <span v-if="showPostLink" @click="toPost"><h6><i class="fa fa-external-link" style="color: white; margin: 12px; cursor: pointer;"/></h6></span>
      </div>
    </div>

    <!-- IFRAME -->
    <iframe v-bind:class="showPostLink ? 'card-content':'post-content'" v-if="contentCard.type === 'iframe'" :src="contentCard.iFrameUrl"
      allowtransparency="true"
      frameborder="0"
      allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    />

    <!-- IMAGE -->
    <img v-bind:class="showPostLink ? 'card-content':'post-content'" v-if="contentCard.type === 'image'" :src="contentCard.previewImage" style="cursor: pointer;" @click="toPost" />

    <!-- VIDEO -->
    <video v-bind:class="showPostLink ? 'card-content':'post-content'" v-if="contentCard.type === 'video'" :src="contentCard.previewVideo" controls />

  </div> 
</template>

<script lang="ts">
import { ref } from 'vue'
import { BaseContentCard } from '@/types/BaseContentCard'
import updateCardValue from '@/services/updateCardValue'
import router from '@/router'

export default {
  props: {
    editMode: Boolean,
    userUid: String,
    postUid: String,
    contentCard: BaseContentCard,
    onDelete: Function,
    showPostLink: {
      type: Boolean,
      default: true
    }
  },
  async setup (props) {
    const contentOptions = [
      ...props.contentCard.rawImages?.map(img => ({
        type: "image",
        previewImage: img
      })) || [],
      ...props.contentCard.rawVideos?.map(vid => ({
        type: "video",
        previewVideo: vid
      })) || [],
    ];
    if (props.contentCard.iFrameUrl) {
      contentOptions.push({
        type: 'iframe',
      })
    }

    let optIndex = 0;
    const hasMorePreviews = ref(contentOptions.length > 1);
    const changePreview = async () => {
      optIndex += 1;
      if (optIndex > contentOptions.length - 1) {
        optIndex = 0;
      }
      const { type, previewImage, previewVideo } = contentOptions[optIndex];
      if (props.contentCard.type !== type) {
        await Promise.all([
          updateCardValue(props.postUid, "type", type),
          updateCardValue(
            props.postUid,
            type === "image" ? "previewImage" : "previewVideo",
            type === "image" ? previewImage : previewVideo
          )
        ]);
      } else {
        await updateCardValue(
            props.postUid,
            type === "image" ? "previewImage" : "previewVideo",
            type === "image" ? previewImage : previewVideo
        )
      }
      console.log({changePreview})
    };

    const toPost = () => router.replace(`/u/${props.userUid}/p/${props.postUid}`)

    return {
      changePreview,
      hasMorePreviews,
      toPost
    };
  }
}
</script>


<style lang="css">
/* Custom CSS here */

.card-content {
  height: 200px;
  /* width: 200px; */
  object-fit: cover;
}
</style>

