import { auth, getTimelineCollection } from '@/firebase.config'
import { BaseContentCard } from '@/types/BaseContentCard';

const updateCardValue = (docUid: string, field: keyof BaseContentCard, newValue: string): Promise<void> | void => {
    const { currentUser } = auth
    const { uid } = currentUser || {}
    if (!uid) return
    const docRef = getTimelineCollection(uid).doc(docUid)
    return docRef.update(field, newValue)
}

export default updateCardValue
